export default {
  data: function() {
    return {
      jsonData: "",
      columns1: [
        {
          title: "",
          key: "key"
        },
        {
          title: "",
          key: "value"
        }
      ],
      data1: [],
      input_data: ""
    };
  },
  mounted() {
    var _this = this;
    this.$http({
      method: "GET",
      url: _this.$store.state.ip + "blockchainforuser"
    }).then(response => {
      _this.jsonData = response.data;
      _this.dealData();
    });
  },
  methods: {
    dealData: function() {
      for (var key in this.jsonData) {
        var keyValue = {
          key: key,
          value: this.jsonData[key]
        };
        this.data1.push(keyValue);
      }
    },
    upload: function() {
      var _this = this;
      this.$http({
        method: "POST",
        url: _this.$store.state.ip + "blockchainforupload",
        headers: {
          "Content-Type": "application/json"
        },
        body: {
          info: _this.input_data
        }
      }).then(response => {
        console.log(response.data);
      });
    }
  }
};



