export default {
  data: function() {
    return {
      blocksData: [],
      blocks: [],
      columns: [
        {
          title: "",
          key: "key"
        },
        {
          title: "",
          key: "value"
        }
      ],
      data: []
    };
  },
  mounted() {
    var _this = this;
    this.$http({
      method: "GET",
      url: _this.$store.state.ip + "blockchain"
    }).then(response => {
      _this.blocksData = response.data.blocks;
      _this.dealData();
    });
  },
  methods: {
    rowClassName(row, index) {
      if (row.flag) {
        return "demo-table-info-row";
      }
      return "";
    },
    dealData: function() {
      for (var i = 0; i < this.blocksData.length; i++) {
        var data = [];
        for (var key in this.blocksData[i]) {
          if (key == "data") {
            for (var dataKey in this.blocksData[i]["data"]) {
              var keyValue = {
                key: dataKey,
                value: this.blocksData[i]["data"][dataKey],
                flag: true
              };
              data.push(keyValue);
            }
          } else if (key == "blockindex") {
            var keyValue = {
              key: key,
              value: this.blocksData[i][key],
              cellClassName: {
                value: "demo-table-info-cell-index"
              }
            };
            data.push(keyValue);
          } else {
            var keyValue = {
              key: key,
              value: this.blocksData[i][key]
            };
            data.push(keyValue);
          }
        }
        if (i == this.blocksData.length - 1) {
          var blockTmp = {
            data: data,
            columns: this.columns,
            flag: false
          };
        } else {
          var blockTmp = {
            data: data,
            columns: this.columns,
            flag: true
          };
        }
        this.blocks.push(blockTmp);
      }
    }
  }
};



